.profile__options{
    width: 250px;
    position: absolute;
    top: 99%;
    right: 0px;
    border-radius: 8px;
    z-index: 999;
    box-shadow: 0 0 10px 2px rgba(0,0,0,0.07),
                0 0 15px 3px rgba(0,0,0,0.05) ;
    background-color: var(--raise-three);
}

.profile__options ul {
    padding: 15px 10px;
    margin-bottom: 10px;
}

.profile__options li {
    list-style: none;
    padding: 8px 5px;
    border-radius: 5px;
}

.profile__options li:hover{
    background-color: var(--highlight-grey);
}


.demacation{
    border-bottom: var(--profile-demacation-border);
    margin: 0 10px;
}


.ul_opt a{
    color: inherit;
    width: 100%;
    height: 100%;
    display: block;
}